@font-face {
    font-family: 'Zig';
    src: local('Zig'), url(../fonts/zig.ttf) format('truetype');
  }

.naviCustom {
    font-family: Zig;
}
.naviTitle {
    color: greenyellow;
    font-size: 1.4em;
    padding: 0;
    padding-right: 0.3em;
}
.naviTitle:hover {
    cursor: default;
}
.linkColor {
    color: white;
}
.linkColor:hover {
    color: greenyellow;
    text-decoration: underline;
}